import React, { useState } from 'react'
import Select from 'react-select';
import {
  CButton,
  CCard,
  CCardBody,
  CFormGroup,
  CSelect,
  CTabs,
  CNav,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CLabel,
  CTextarea,
  CInput,
  CCardHeader,
  CDataTable,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

import {fieldhostbl, jns_manifest, jns_moda, datakpbcs, kelpos, pelabuhan, kodehs } from './Datasource.js'

const Tambahbarang = (props) => {

  const [modal, setModal] = useState(false)
  const [value, setValue] = useState({uraian_btki:''})
  const [no, setNo] = useState(1)
  const handleReactSelect = (selectedOption, { name }) => {
    setValue(prevState => ({...prevState, no: no, idhost: props.idhost, [name] : selectedOption.value, uraian_btki : selectedOption.label  }));
  }
  const handleClick = () => {
      props.calleback(value)
      setNo(no + 1)
      setModal(false)
      setValue({uraian_btki:''})
  }
  console.log('ÜJI COBA TAMBAH BARANG', value)
  return (
    <>
      <CButton 
        onClick={() => {
            if (props.statusheader){
            setModal(!modal)
            } else {
            alert("Simpan Data Header terlebih dahulu")
            } 
        }}
        className="mr-1"
        color="info"
        size="sm"
      >
        <a>Tambah</a>
      </CButton>
      <CModal 
        show={modal} 
        onClose={setModal}
        size="l"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Tambah Barang</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CRow>
            <CCol sm="9">
                <CFormGroup>
                <CLabel htmlFor="kodehs">Kode HS</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="kodehs" options={kodehs} />
                </CFormGroup>
                <CFormGroup>
                <CLabel htmlFor="kelpos">Uraian BTKI</CLabel>
                <CTextarea rows="5" value={value.uraian_btki} name="uraian_btki" disabled/>
                </CFormGroup>               
            </CCol>
          </CRow>                                                                       
        </CModalBody>
        <CModalFooter>
          <CButton onClick={handleClick.bind(this)} color="primary">Tambah</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}    

export default Tambahbarang