import React, { useState } from 'react'
import Select from 'react-select';
import {
  CButton,
  CCard,
  CCardBody,
  CFormGroup,
  CSelect,
  CTabs,
  CNav,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CLabel,
  CTextarea,
  CInput,
  CCardHeader,
  CDataTable,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

import {fieldhostbl, jns_manifest, jns_moda, datakpbcs, kelpos, pelabuhan, kodehs, status_kontainer, jns_kontainer, tipe_kontainer, ukuran } from './Datasource.js'

const Tambahkontainer = (props) => {

  const [modal, setModal] = useState(false)
  const [value, setValue] = useState('')
  const [no, setNo] = useState(1)
  const handleReactSelect = (selectedOption, { name }) => {
    setValue(prevState => ({...prevState, no: no, idhost: props.idhost, [name] : selectedOption.label}));
  }
  const handleChange = (e) => {
      const nama = e.target.name
      const value = e.target.value
    setValue(prevState => ({...prevState, [nama]: value}));
  }
  const handleClick = () => {
      props.calleback(value)
      setNo(no + 1)
      setModal(false)
      setValue('')
  }
  console.log('ÜJI COBA TAMBAH KONTAINER', value)
  return (
    <>
      <CButton 
        onClick={() => {
            if (props.statusheader){
            setModal(!modal)
            } else {
            alert("Simpan Data Header terlebih dahulu")
            } 
        }}
        className="mr-1"
        color="info"
        size="sm"
      >
        <a>Tambah</a>
      </CButton>
      <CModal 
        show={modal} 
        onClose={setModal}
        size="xl"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Tambah Kontainer</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CRow>
            <CCol sm="6">
                <CFormGroup>
                <CLabel htmlFor="no_container">No Kontainer</CLabel>
                <CInput onChange={handleChange.bind(this)} name ="no_container" />
                </CFormGroup>
                <CFormGroup>
                <CLabel htmlFor="ukuran">Ukuran</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="ukuran" options={ukuran} />
                </CFormGroup> 
                <CFormGroup>
                <CLabel htmlFor="jenis">Jenis</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="jenis" options={jns_kontainer} />
                </CFormGroup>                               
            </CCol>
            <CCol sm="6">
                <CFormGroup>
                <CLabel htmlFor="no_segel">No Segel</CLabel>
                <CInput onChange={handleChange.bind(this)} name ="no_segel" />
                </CFormGroup>
                <CFormGroup>
                <CLabel htmlFor="tipe">Tipe</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="tipe" options={tipe_kontainer} />
                </CFormGroup> 
                <CFormGroup>
                <CLabel htmlFor="status">Status</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="status" options={status_kontainer} />
                </CFormGroup>                               
            </CCol>            
          </CRow>                                                                       
        </CModalBody>
        <CModalFooter>
          <CButton onClick={handleClick.bind(this)} color="primary">Tambah</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}    

export default Tambahkontainer