import React, { useState } from 'react'
import Select from 'react-select';
import {
  CButton,
  CCard,
  CCardBody,
  CFormGroup,
  CSelect,
  CTabs,
  CNav,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CLabel,
  CTextarea,
  CInput,
  CCardHeader,
  CDataTable,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

import {fieldhostbl, jns_manifest, jns_moda, datakpbcs, kelpos, pelabuhan, kodehs, status_kontainer, jns_kontainer, tipe_kontainer, ukuran, jns_dok } from './Datasource.js'

const Tambahdokumen = (props) => {

  const [modal, setModal] = useState(false)
  const [value, setValue] = useState('')
  const [no, setNo] = useState(1)
  const handleReactSelect = (selectedOption, { name }) => {
    setValue(prevState => ({...prevState, no: no, idhost: props.idhost, [name] : selectedOption.label}));
  }
  const handleChange = (e) => {
      const nama = e.target.name
      const value = e.target.value
    setValue(prevState => ({...prevState, [nama]: value}));
  }
  const handleClick = () => {
      props.calleback(value)
      setNo(no + 1)
      setModal(false)
      setValue('')
  }
  console.log('ÜJI COBA TAMBAH DOKUMEN', value)
  return (
    <>
      <CButton 
        onClick={() => {
            if (props.statusheader){
            setModal(!modal)
            } else {
            alert("Simpan Data Header terlebih dahulu")
            } 
        }} 
        className="mr-1"
        color="info"
        size="sm"
      >
        <a>Tambah</a>
      </CButton>
      <CModal 
        show={modal} 
        onClose={setModal}
        size="l"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Tambah Dokumen</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CRow>
            <CCol sm="6">
                <CFormGroup>
                <CLabel htmlFor="jns_dok">Jenis Dokumen</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="jns_dok" options={jns_dok} />
                </CFormGroup>
                <CFormGroup>
                <CLabel htmlFor="no_dok">Nomor Dokumen</CLabel>
                <CInput onChange={handleChange.bind(this)} name ="no_dok" />
                </CFormGroup>                                
            </CCol>
            <CCol sm="6">
            <CFormGroup>
                <CLabel htmlFor="kantor">Kantor</CLabel>
                <Select onChange = {handleReactSelect.bind(this)} name ="kantor" options={datakpbcs} />
                </CFormGroup>
                <CFormGroup>
                <CLabel htmlFor="tgl_dok">Tanggal Dokumen</CLabel>
                <CInput onChange={handleChange.bind(this)} name ="tgl_dok" type="date" />
                </CFormGroup>                               
            </CCol>            
          </CRow>                                                                       
        </CModalBody>
        <CModalFooter>
          <CButton onClick={handleClick.bind(this)} color="primary">Tambah</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}    

export default Tambahdokumen