import React, { useState } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CDataTable,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

import {fieldhostbl, jns_manifest, jns_moda, datakpbcs, kelpos } from './Datasource.js'
import Detailhostbl from './Detailhostbl.js'

const Detail = (props) => {

  const [modal, setModal] = useState(false)
  const [datablhost, setDatablhost] = useState([])
  const filteran = datablhost.filter(item => item.parentid == props.idmaster)
  const jmlaktualhouse = filteran.length
  console.log ('WUEKEKEKEKEKEK', props.jmlhouse)
  const handleClick = () => {
    if(filteran.length){
      const parentid = filteran[0].parentid
      props.calleback(parentid, jmlaktualhouse)
      setModal(false)
    } else {
      setModal(false)
    }
  }
  return (
    <CRow>
      <div className="pl-2">
      <CButton 
        onClick={() => setModal(!modal)} 
        color="info"
        size="sm"
      >
        detail
      </CButton>
      </div>
      <div className="pl-2">
      <CButton size="sm" color="success">Edit</CButton>{'  '}        
      </div>
      <div className="pl-2">
      <CButton size="sm" color="danger">Hapus</CButton>{'  '}        
      </div>    
      <CModal 
        show={modal} 
        onClose={setModal}
        size="xl"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Daftar Host BL</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="p-2 d-flex justify-content-end">
          <CRow>
          <Detailhostbl parentid ={props.idmaster} nomaster={props.nomaster} tglmaster={props.tglmaster} kelpos={props.kelpos} noaju={props.noaju} jmlhouse={props.jmlhouse} jmlaktualhouse={jmlaktualhouse} calleback={datablhost => setDatablhost(prevState => ([...prevState, datablhost]))} />
          </CRow>
          </div>
          <CRow>
            <CCol xs="12">
              <CDataTable
                items={filteran}
                fields={fieldhostbl}
                itemsPerPage={5}
                pagination   
                scopedSlots = {
                  {
                    'action' :
                    (item,index) => {
                      return (
                        <td>
                          <CRow>
                          <div className="pl-2"><CButton className="pl-2"  color="info" size="sm">Lihat</CButton></div>
                          <div className="pl-2"><CButton className="pl-2" size="sm" color="success">Edit</CButton></div>       
                          <div className="pl-2"><CButton className="pl-2" size="sm" color="danger">Hapus</CButton></div> 
                          </CRow>                                                                  
                        </td>
                        )
                    }                                                       
                  }                         
              }                                   
              />
              </CCol>
          </CRow>
        </CModalBody>
        <CModalFooter>
          <CButton onClick={handleClick.bind(this)} color="primary">Simpan</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </CRow>
  )
}    

export default Detail